@import "variables";
.login {
  color: $color-default;
  background: #003333;
  h1 a {
    background-image: url(../../images/admin/logo.png) !important;
    background-size: 100%;
    background-position: center top;
    background-repeat: no-repeat;
    margin: 0 auto 24px;
    padding: 0;
    width: 314px;
    height: 87px;
    text-indent: -9999px;
    outline: 0;
    display: block;
  }
  label {
    color: $color-default;
  }
  input[type="checkbox"] {
    box-shadow: none;
    border-color: $color-primary;
  }
  input[type="checkbox"]:checked:before {
    color: $color-primary;
  }
  .button-primary {
    background: $color-primary;
    border: none;
    -webkit-box-shadow: none;
    box-shadow: none;
    color: #fff;
    text-decoration: none;
    text-shadow: none;
    transition: $transition;
  }
  .button-primary:active,
  .button-primary:hover {
    background-color: $color-secondary;
    border: none;
    -webkit-box-shadow: none;
    box-shadow: none;
    text-shadow: none;
  }
  #backtoblog a,
  #nav a {
    color: $color-primary;
  }
  #backtoblog a:hover,
  #nav a:hover {
    color: $color-secondary;
  }
  #login_error,
  .message {
    color: $color-default;
    border-left-color: $color-primary;
  }
}
